
body{
  margin: 0;
  padding: 0;
  background-color:rgba(0, 0, 7, 0.879) ;
  color: rgb(240, 245, 245);
  font-family: 'Courier New', Courier, monospace;
}
.grid-container {
  display: grid;
  grid-template-columns: 280px 1fr; /* Sidebar fixed width, main content flexible */
  grid-template-rows: 100vh; /* Full height for both columns */
  grid-template-areas: 'sidebar main';
  height: 100vh; /* Full viewport height */
  margin: 0;
  padding: 0;
}
