.Cardbody {
  background-color: #080808;
  font-family: "Courier New" Courier monospace;
}
.card {
  margin: 50px;
  width: 90%;
  flex: 1;
  /* Change this value as per your desired width */
  max-width: 1200px;
  border-radius: 20px;
  padding: 10px;
  border: 2px solid aqua;
  background-color: #080808;
}

.employee-role-container,
.employee-phone-container,
.employee-email-container {
  display: flex;
  align-items: center;
}
.emp {
  display: flex;
}
.emp p {
  margin-right: 20px;
  /* Add space between elements */
}
/* card-header { */
/* display: flex;
    justify-content: space-between;
    align-items: center; */
/* } */

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* Adjust the gap as needed */
}

.card-actions {
  display: flex;
  gap: 10px;
  margin-left: auto;
  /* Adjust the gap as needed */
}
.card-title {
  font-size: 30px;
  font-weight: bold;
}
.card-title::before {
  content: "|";
  /* Add the vertical line before the title */
  color: #00ffff;
  /* Color of the vertical line */
  font-size: 36px;
  /* Increase the size of the line */
  margin-right: 10px;
  /* Space between the line and the title */
}
.card-button {
  padding: 10px;
  border-radius: 20px;
  color: #f1ebeb;
  cursor: pointer;
  font-weight: bold;
  border: 1px solid #00ffff;
}
.card-button1 {
  padding: 10px;
  border-radius: 20px;
  border: none;
  background-color: #00ffff;
  color: black;
  cursor: pointer;
  font-weight: bold;
}

.employee-role-label,
.employee-phone-label,
.employee-email-label {
  margin-right: 10px;
  /* Adjust spacing between label and value */
}
.employee-info {
  display: flex;
  align-items: center;
}
.employee-name-actions {
  font-size: 20px;
}

.card-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 20px;
}

.employee-details {
  margin-left: 20px;
}

.employee-name {
  font-size: 25px;
  font-weight: bold;
}

.employee-role,
.employee-phone,
.employee-email {
  margin-top: 5px;
  justify-content: space-between;
}
.card-stats {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  /* flex-wrap: wrap; */
}

.card-stat {
  flex: 1 1 calc(33.33% - 20px);
  /* Three cards per row with spacing */
  border-radius: 20px;
  padding: 10px;
  /* Increased padding for better content spacing */
  border: 2px solid #474545;
  margin: 10px;
  /* Add margin between cards */
  box-sizing: border-box;
  /* Ensure padding is included in width calculations */
  display: flex;
  flex-direction: column;
  max-width: 280px;
  /* Increased width */
  max-height: 300px;
  background-color: #080808;
  /* Increased height */
}

.card-stat-number {
  font-size: 20px;
  font-weight: bold;
}

.card-stat-text {
  margin-top: 5px;
  font-size: medium;
}
/* employee history */
.history-card {
  margin: 50px;
  width: 90%;
  flex: 1;
  /* Adjust width value for responsiveness */
  max-width: 1200px;
  /* Increase the maximum width */
  border-radius: 20px;
  margin-top: 5px;
  padding: 10px;
  padding-right: 30px;
  border: 2px solid aqua;
  background-color: #080808;
}

.history-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.history-title {
  font-size: 30px;
  font-weight: bold;
}
.history-title::before {
  content: "|";
  /* Add the vertical line before the title */
  color: #00ffff;
  /* Color of the vertical line */
  font-size: 36px;
  /* Increase the size of the line */
  margin-right: 10px;
  /* Space between the line and the title */
}
.history-actions {
  display: flex;
  gap: 10px;
  margin-left: auto;
}

.history-btn {
  padding: 10px;
  border-radius: 10px;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  font-weight: bold;
  border: 1px solid #00ffff;
}

.history-card-content {
  margin-top: 5px;
}

.history-stats {
  display: flex;
  justify-content: space-between;
  /* margin-top: 10px; */
  flex-wrap: wrap;
}

.history-stat {
  flex: 1 1 calc(33.33% - 20px);
  /* Three cards per row with spacing */
  /* Adjust width to fit three per row, with some margin */
  border-radius: 20px;
  padding: 20px;
  border: 2px solid #474545;
  background-color: #080808;
  margin: 10px;
  /* Add margin between cards */
  box-sizing: border-box;
  /* Ensure padding is included in width calculations */
  display: flex;
  flex-direction: column;
  max-width: 300px;
  /* Adjust width as needed */
  max-height: 500px;
  /* Increased height */
  /* Adjust as needed to control card width */
}

.history-stat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
  /* Reduced space below the header */
}

.history-stat-number {
  font-size: 16px;
  font-weight: bold;
  white-space: nowrap;
  /* Prevent text from breaking into a new line */
}

.hist-btn {
  padding: 6px;
  margin: 5px;
  border-radius: 10px;
  border: 1px solid #00ffff;
  /* Add border to maintain visibility */
  background-color: transparent;
  /* Set background to transparent */
  color: #ffffff;
  /* Set text color to match the border color */
  cursor: pointer;
  font-weight: bold;
  /* Optional: Make text bold for better visibility */
}

.history-stat-times {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
}

.history-stat-text {
  font-size: 12px;
  color: #b9b1b1;
  margin: 0;
}
.highlight-time {
  color: white;
  /* Set text color to white */
  font-size: 20px;
  /* Increase font size */
  font-weight: bold;
  /* Make text bold */
}
